import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles, AppBar, Toolbar, Container } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import MenuItem from '@material-ui/core/MenuItem';
import SaveIcon from '@material-ui/icons/Save';
import { useState, useEffect } from "react"
import { Slide } from "@material-ui/core"

import firebase from "../firebase"
import axios from "axios"
import cheerio from "cheerio"

import CancelIcon from '@material-ui/icons/Cancel';
import DescriptionIcon from '@material-ui/icons/Description';

const useStyles = makeStyles(theme => ({
  fabAdd: {
		position: "fixed",
		bottom: theme.spacing(9),
		right: theme.spacing(2),
		zIndex: 1000
  },
  formInput: {
    width: "100%",
    margin: theme.spacing(1,0)
  },
  root: {
    padding: theme.spacing(8, 0, 18, 0)
  },
  icon: {
    marginRight: theme.spacing(1)
  },
  bottomAppBar: {
    top: "auto",
    bottom: 0
  },
}))

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="left" ref={ref} {...props} />
})

export default function FormDialog(props) {

  console.log("FORM DIALOG PROPS: ", props)
  const classes = useStyles();
  const folders = useDetails()
  const [open, setOpen] = useState(false)

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    resetFields()
    setOpen(false)
  }

  const [linkInFolder, setLinkInFolder] = useState(props.defaultFolder)
  const [linkTitle, setLinkTitle] = useState("")
  const [linkUrl, setLinkUrl] = useState("")
  const [linkImg, setLinkImg] = useState("")

  const handleFolderSelect = e => {
    setLinkInFolder(e.target.value)
  }

  const handleTitleInput = e => {
    setLinkTitle(e.target.value)
  }

  const handleUrlInput = e => {
    setLinkUrl(e.target.value)
  }

  const [collectionName, setCollectionName] = useState("")
  const [collectionDesc, setCollectionDesc] = useState("")

  const handleCollectionNameInput = e => {
    setCollectionName(e.target.value)
  }

  const handleCollectionDescInput = e => {
    setCollectionDesc(e.target.value)
  }

  const handleFormSubmit = e => {

    e.preventDefault()
    switch(props.mode) {
      case "collection": {
        firebase.firestore()
        .collection("collections")
        .add({
          folder_name: collectionName,
          folder_desc: collectionDesc
        })
        break
      }
      default: 
        firebase.firestore()
          .collection("links")
          .add({
            link_title: linkTitle,
            link_url: linkUrl,
            folder_id: linkInFolder,
            link_img: linkImg
          }).then(() => {
            props.onUpdate("Link successfully added")
            console.log("Link successfully added")
          }).catch(err => {
            props.onUpdate("Error adding link: ", err)
            console.error("Error adding to database: ", err)
          })
    }
    resetFields()
    setOpen(false)
    
  }

  function resetFields() {
    setLinkInFolder("3ppmoaOw6ANJk10oWmhh")
    setLinkTitle("")
    setLinkUrl("")
    setCollectionName("")
    setCollectionDesc("")
  }

  const handleGetDetails = (url) => {
    axios.get("https://cors-anywhere.herokuapp.com/" + url)
      .then(
        res => {
          console.log(res.data)
          const $ = cheerio.load(res.data)
          let title = $("meta[property='og:title']").attr("content")
          if(title === undefined) title = $('title')[0].children[0].data
          if(title === undefined) title = ""
          let img = $("meta[property='og:image']").attr("content")
          if(img === undefined) img = $("meta[property='twitter:image']").attr("content")
          if(img === undefined) {
            $("img").each((i, link) => {
              if(/^https:\/\//.test(link.attribs.src)) {
                img = link.attribs.src
              }
            })
          }
          if(img === undefined) img = ""
          
          setLinkImg(img)
          setLinkTitle(title)
      })
  }

  const BottomAppBar = () => {
    return (
      <AppBar position="fixed" color="secondary" className={classes.bottomAppBar}>
        <Toolbar>
          {props.mode === "link" ? (
            <Button className={classes.icon} onClick={() => handleGetDetails(linkUrl)} >
              <DescriptionIcon className={classes.icon} /> Get details
            </Button>
          ) : ( "" )}
          <Button onClick={handleClose} >
            <CancelIcon className={classes.icon} /> Cancel
          </Button>
        </Toolbar>
      </AppBar>
    )
  }

  return (
    <div>
      <Fab color="secondary" aria-label="add"
        className={classes.fabAdd}
        onClick={handleClickOpen}
      >
        <AddIcon />
      </Fab>
      <Dialog fullScreen open={open}
        TransitionComponent={Transition} 
        aria-labelledby="form-dialog-title"
      >
        <form onSubmit={handleFormSubmit} >
        <div className={classes.root}>
          <DialogTitle id="form-dialog-title">
            Add new {props.mode === "collection" ? props.mode : "link"}
          </DialogTitle>
          <DialogContent>
            {props.mode === "collection"
              ? <>
                  <TextField
                    required
                    multiline
                    autoFocus
                    className={classes.formInput}
                    id="collection-name"
                    label="Collection name"
                    variant="outlined"
                    value={collectionName}
                    onChange={handleCollectionNameInput}
                    size="small"
                  />
                  <TextField
                    multiline
                    className={classes.formInput}
                    id="collection-desc"
                    label="Collection description"
                    variant="outlined"
                    value={collectionDesc}
                    onChange={handleCollectionDescInput}
                    size="small"
                  />
              </> : <>
                  <TextField
                    multiline
                    className={classes.formInput}
                    id={"link-title"}
                    label="Link title"
                    variant="outlined"
                    value={linkTitle}
                    onChange={handleTitleInput}
                    size="small"
                  />
                  <TextField
                    required
                    multiline
                    autoFocus
                    type="url"
                    className={classes.formInput}
                    id="link-url"
                    label="Link URL"
                    variant="outlined"
                    size="small"
                    value={linkUrl}
                    onChange={handleUrlInput}
                    helperText="URL must start with http:// or https://"
                  />
                  <TextField
                    className={classes.formInput}
                    id="link-in-folder-id"
                    select
                    label="Collection"
                    value={linkInFolder}
                    onChange={handleFolderSelect}
                    variant="outlined"
                    size="small"
                  >
                    {folders.map((option) => (
                        <MenuItem 
                          key={option.folder_id} 
                          value={option.folder_id}
                        >
                          {option.folder_name}
                        </MenuItem>
                    ))}
                  </TextField>
                  {/* <Container style={{
                    marginTop: 8,
                    width: "100%",
                    height: 200,
                    objectFit: "contain",
                  }}>
                    Link image preview: 
                    <img width={"100%"} height={200} src={linkImg}></img>
                  </Container> */}
                </>
            }
          </DialogContent>
          </div>
          <Fab color="secondary" aria-label="add"
            className={classes.fabAdd}
            type="submit"
            variant="extended"
          >
            <SaveIcon className={classes.icon} /> Save
          </Fab>
        </form>
        <BottomAppBar />
      </Dialog>
      
    </div>
  );
}

function useDetails() {
	const [folderDetails, setFolderDetails] = useState([])

	useEffect(() => {
    const unsubscribe = firebase
			.firestore()
			.collection("collections")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					folder_id: doc.id,
					...doc.data()
				}))
				setFolderDetails(newDetails)
			})
        
      return () => unsubscribe()
    }, [])
    
    return folderDetails
}