import React from "react"
import { useState, useEffect } from "react"
import firebase from "../firebase"
import { Grid, makeStyles, Typography, Container } from "@material-ui/core"
import StackGrid from "react-stack-grid"
import LinkCard from "../components/LinkCards"

import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import FormDialog from "../components/FormDialog"

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
		width: `calc(100vw - ${theme.spacing(2)}px)`,
		marginLeft: theme.spacing(1),
		marginRight: theme.spacing(1),
		marginTop: theme.spacing(8),
		marginBottom: theme.spacing(18)
  },
  folderNameHeader: {
    background: theme.palette.background.paper,
    position: "sticky",
    top: theme.spacing(7),
    zIndex: 5,
    paddingTop: theme.spacing(2)
  },
  snackBar: {
		position: "fixed",
		bottom: theme.spacing(18)
	}
}))

const Folder = ({ id, name }) => {

  const [open, setOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("")
  
  const handleClick = (message) => {
		setOpen(true)
    setSnackbarMessage(message)
  }

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return
    }
    setOpen(false)
	}
  
  const linkData = useDetails(id)
  const classes = useStyles()
  console.log("Folder: ", linkData)
  const links = linkData.map((link) => (
    <Grid item key={link.link_id}>
      <LinkCard 
        contentId={link.link_id}
        title={link.link_title}
        url={link.link_url}
        img={link.link_img}
        onClickDelete={handleClick}
      />
    </Grid>
  ))
  
  const size = useWindowSize()
  const cardWidth = () => {
    if (size.width < 321) return "100%"
    else if (size.width < 426) return "50%"
    else if (size.width < 769) return "33.3333333%"
    else if (size.width < 1025) return "25%"
    else return "20%"
  }
    
  return (
    <>
      <Container className={classes.folderNameHeader}>
        <Typography variant="h6" gutterBottom>{name}</Typography>
      </Container>
      <div className={classes.root} >
        <StackGrid 
          monitorImagesLoaded={true}
          columnWidth = {cardWidth()}
          gutterWidth={8}
          gutterHeight={8}
        >
          {links}
        </StackGrid>
        <Snackbar
          className={classes.snackBar}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={open}
          autoHideDuration={6000}
          onClose={handleClose}
          message={snackbarMessage}
          action={
            <>
              <IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
                <CloseIcon fontSize="small" />
              </IconButton>
            </>
          }
        />
      </div>
      <FormDialog mode={"link"} onUpdate={handleClick} defaultFolder={id}/>
    </>
  )
}

function useWindowSize() {
  // Initialize state with undefined width/height so server and client renders match
  // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
  const [windowSize, setWindowSize] = useState({
      width: undefined,
      height: undefined,
  })

  useEffect(() => {
      // Handler to call on window resize
      function handleResize() {
      // Set window width/height to state
          setWindowSize({
              width: window.innerWidth,
              height: window.innerHeight,
          })
      }
      // Add event listener
      window.addEventListener("resize", handleResize);
      // Call handler right away so state gets updated with initial window size
      handleResize()
      // Remove event listener on cleanup
      return () => window.removeEventListener("resize", handleResize);
  }, []) // Empty array ensures that effect is only run on mount

  return windowSize;
}

function useDetails(id) {

	const [linkDetails, setLinkDetails] = useState([])

	useEffect(() => {
    const linkRef = firebase.firestore().collection("links")
    if(id === "") {
      linkRef.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					link_id: doc.id,
					...doc.data()
				}))
				setLinkDetails(newDetails)
			})
    } else {
      linkRef.where("folder_id", "==", id)
      .onSnapshot(snapshot => {
        const newDetails = snapshot.docs.map((doc) => ({
          link_id: doc.id,
          ...doc.data()
        }))
        setLinkDetails(newDetails)
      })
    }
    return () => linkRef
  }, [id])

  // TO-DO: CLEANUP FUNCTION
  
  console.log("Function useDetails() in Folder.js called")
	
	return linkDetails
}

export default Folder